import React from 'react';
import iconTicketTrain from '../../../images/icon_ticket_train.png';
import iconArrowRightGray from '../../../images/icon_arrow_right_gray.png';
import iconArrowRightBlack from '../../../images/icon_arrow_right_black.png';
import iconSearchThere from '../../../images/icon_search_there.png';
import iconSearchBack from '../../../images/icon_search_back.png';
import TicketPriceAndSeats from './TicketPriceAndSeats';
import { NavLink } from 'react-router-dom';
import { setTrainIdAC } from '../../../redux/action';
import { connect } from 'react-redux';


class ResultSearchTickets extends React.Component {

	setTrainId = () => this.props.setTrainId(this.props.state.departure._id);

	render() {

		let fromDateTime = this.props.state.departure.from.datetime;
		let toDateTime = this.props.state.departure.to.datetime;
		let duration = this.props.state.departure.duration;
		let fromArrival = fromDateTime + duration;
		let toArrival = toDateTime + duration;

		const getHours = (msc) => new Date(msc).getHours();
		const getMinutes = (msc) => (new Date(msc).getMinutes() < 10 ? '0' : '') + new Date(msc).getMinutes();

		return (
			<div className="result-search-of-tickets row mt-4">
				<div className="select-tickets-search-number-train col-lg-3 pt-5 pb-5">
					<img className="pl-5" src={iconTicketTrain} alt="иконка поезда" />
					<h5 className="text-center">{this.props.state.departure.train.name}</h5>
					<p className="pl-3 pt-5 font-weight-light">Адлер<img className="pl-2" src={iconArrowRightGray}
						alt="..." /></p>
					<p className="pl-3">{this.props.state.departure.from.city.name}<img className="pl-2" src={iconArrowRightBlack} alt="..." /></p>
					<p className="pl-3">{this.props.state.departure.to.city.name}</p>
				</div>
				<div className="col-lg-9">
					<div className="row h-100">
						<div className="col-lg-8">
							<div className="row pl-4 pr-4 pt-5 justify-content-between">
								<div>
									<h5>{getHours(fromDateTime)}:{getMinutes(fromDateTime)}</h5>
									<p>{this.props.state.departure.from.city.name}</p>
									<p className="font-weight-light">{this.props.state.departure.from.railway_station_name}</p>
								</div>
								<div className="pl-4">
									<p className="font-weight-light">{getHours(duration)}:{getMinutes(duration)}</p>
									<img src={iconSearchThere} alt="иконка стрелки вправо" />
								</div>
								<div className="pl-4">
									<h5>{getHours(fromArrival)}:{getMinutes(fromArrival)}</h5>
									<p>{this.props.state.departure.to.city.name}</p>
									<p className="font-weight-light">{this.props.state.departure.to.railway_station_name}</p>
								</div>
							</div>
							<div className="row pl-4 pr-4 pt-5 justify-content-between">
								<div>
									<h5>{getHours(toArrival)}:{getMinutes(toArrival)}</h5>
									<p>Москва</p>
									<p className="font-weight-light">{this.props.state.departure.from.railway_station_name}</p>
								</div>
								<div className="pl-4">
									<p className="font-weight-light">{getHours(duration)}:{getMinutes(duration)}</p>
									<img src={iconSearchBack} alt="иконка стрелки влево" />
								</div>
								<div className="pl-4">
									<h5>{getHours(toDateTime)}:{getMinutes(toDateTime)}</h5>
									<p>Санкт-Петербург</p>
									<p className="font-weight-light">{this.props.state.departure.to.railway_station_name}</p>
								</div>
							</div>
						</div>

						<div className="ticketPriceAndSeats col h-100 pl-4 pt-5 pr-4">
							<div>
								{this.props.state.departure.have_fourth_class &&
									<TicketPriceAndSeats name="Сидячий"
										seats={this.props.state.available_seats_info.fourth}
										price={this.props.state.departure.price_info.fourth.top_price} />
								}
								{this.props.state.departure.have_third_class &&
									<TicketPriceAndSeats name="Плацкарт"
										seats={this.props.state.available_seats_info.third}
										price={this.props.state.departure.price_info.third.top_price} />
								}
								{this.props.state.departure.have_second_class &&
									<TicketPriceAndSeats name="Купе"
										seats={this.props.state.available_seats_info.second}
										price={this.props.state.departure.price_info.second.top_price} />
								}
								{this.props.state.departure.have_first_class &&
									<TicketPriceAndSeats name="Люкс"
										seats={this.props.state.available_seats_info.first}
										price={this.props.state.departure.price_info.first.top_price} />
								}
							</div>

							<div className="row pb-3">
								<NavLink className="btn btn-warning m-3"
									to="/seat_selection"
									type="button"
									onClick={this.setTrainId}>Выбрать места
              </NavLink>
							</div>
						</div>
					</div>

				</div>
			</div>
		);
	}
};

const mapDispatchToProps = (dispatch) => {
	return {setTrainId: (trainId) => dispatch(setTrainIdAC(trainId))}
}

export default connect(null, mapDispatchToProps)(ResultSearchTickets);